import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ValidateService } from 'src/app/services/validate.service';

@Component({
  selector: 'app-payinfo',
  templateUrl: './payinfo.component.html',
  styleUrls: ['./payinfo.component.scss']
})
export class PayinfoComponent implements OnInit {
  name: string;
  username: string;
  email: string;
  cardnumber: string;
  expire: string;
  secretnum: number;

  ptoken: string;
  stoken: string;

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router

  ) { }

  ngOnInit() {
    this.ptoken = localStorage.getItem("ptoken")
    this.stoken = localStorage.getItem("stoken")
    if (this.ptoken == null || this.stoken == null) {
      this.flashMessage.show("   .", {
        cssClass: "alert-danger text-center",
        timeout: 3000
      });
      this.router.navigate(["/loginCA"])
    }
  }

  onProceedSubmit() {
    const user = {
      name: this.name,
      username: this.username,
      email: this.email
    }
    const card = {
      cardnumber: this.cardnumber,
      expire: this.expire,
      secretnum: this.secretnum
    }
    //  
    if (!this.validateService.validateProceed(user, card)) {
      this.flashMessage.show("  ", {
        cssClass: "alert-danger text-center",
        timeout: 3000
      });
      return false;
    }

    // 
    if (!this.validateService.validateEmail(user.email)) {
      this.flashMessage.show("   ", {
        cssClass: "alert-danger text-center",
        timeout: 3000
      });
      return false;
    }

    //  
    if (!this.validateService.validateCardnum(card.cardnumber)) {
      this.flashMessage.show("  ", {
        cssClass: "alert-danger text-center",
        timeout: 3000
      });
      return false;
    }

    //  
    if (!this.validateService.validateCardlimit(card.expire)) {
      this.flashMessage.show("   ", {
        cssClass: "alert-danger text-center",
        timeout: 3000
      })
      return false;
    }

    //  
    if (!this.validateService.validateCardSecure(card.secretnum)) {
      this.flashMessage.show("   ", {
        cssClass: "alert-danger text-center",
        timeout: 3000
      })
      return false;
    }


    //  
    this.authService.storeOrderData(user).subscribe(data => {
      if (data.success) {
        this.flashMessage.show(data.msg, {
          cssClass: "alert-success text-center",
          timeout: 3000
        })
        this.router.navigate(["/paysuccess"])
      } else {
        console.log(data)
        this.flashMessage.show(data.msg, {
          cssClass: "alert-danger",
          timeout: 3000
        })
      }
    })



  }
}
